.contacts__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
  margin-left: 29px;
}

.contacts__email {
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  color: #444444;
  margin-left: 29px;
}

.contacts__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  color: #444444;
  margin-left: 107px;
}

.contacts__map-wrapper {
  margin: 0 auto;

  @media (min-width: @desktop-width) {
    width: @desktop-width;
    padding: 0 50px;
  }
}

.contacts-button__write-us {
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #444444;
}
